import { toast } from "react-toastify";
import {
  CREAT_USER,
  GET_ERRORS,
  BEFORE_USER,
  GET_USERS,
  UPSERT_USER,
  DELETE_USER,
} from "../../redux/types";
import { emptyError } from "../../redux/shared/error/error.action";
import { ENV } from "./../../config/config";

export const beforeUser = () => {
  return {
    type: BEFORE_USER,
  };
};

//////
export const createUser = (obj, qs = null) => (dispatch) => {

  dispatch(emptyError());
  let url = `${ENV.url}user/create`;
  if (qs) url += `?${qs}`;
  fetch(url, {
    method: "POST",
    headers: {
      Authorization: ENV.Authorization,
      "x-auth-token": ENV.x_auth_token,
      "x-access-token": localStorage.getItem("accessToken")
        ? window.atob(localStorage.getItem("accessToken"))
        : "",
      // "Content-Type": "application/json",
    },
    body: obj,
  })
    .then((res) => res.json()).then((data) => {
      if (data.success) {
        toast.success(data.message);
        dispatch({
          type: CREAT_USER,
          payload: data.newUser,
        });
      } else {
        if (data.userDisabled) {
          window.location.href = "/admin/login";
          localStorage.removeItem("accessToken");
          localStorage.removeItem("adminId");
          localStorage.removeItem("encuse");
        }
        toast.error(data.message);
        dispatch({
          type: GET_ERRORS,
          payload: data,
        });
      }
    })
    .catch((error) => {
      if (error.response && error.response.data) {
        const { data } = error.response;
        if (data.message) toast.error(data.message);
      }
      dispatch({
        type: GET_ERRORS,
        payload: error,
      });
    });
};


export const getUsers =
  (qs = null) =>
  (dispatch) => {
    dispatch(emptyError());
    let url = `${ENV.url}user/list`;
    if (qs) url += `?${qs}`;
    fetch(url, {
      method: "GET",
      headers: {
        "content-type": "application/json",
        Authorization: ENV.Authorization,
        "x-auth-token": ENV.x_auth_token,
        "x-access-token": localStorage.getItem("accessToken")
          ? window.atob(localStorage.getItem("accessToken"))
          : "",
      },
    })
      .then((res) => res.json()).then((data) => {
        if (data.success) {
          // if (!qs) toast.success(data.message);
          dispatch({
            type: GET_USERS,
            payload: data.data,
          });
        } else {
          if (data.userDisabled) {
            window.location.href = "/admin/login";
            localStorage.removeItem("accessToken");
            localStorage.removeItem("adminId");
            localStorage.removeItem("encuse");
          }
          if (!qs) toast.error(data.message);
          dispatch({
            type: GET_ERRORS,
            payload: data,
          });
        }
      })
      .catch((error) => {
        if (error.response && error.response.data) {
          const { data } = error.response;
          if (data.message) toast.error(data.message);
        }
        dispatch({
          type: GET_ERRORS,
          payload: error,
        });
      });
  };

