import React, { useState, useEffect } from 'react';
import { toast } from 'react-toastify';
import { connect } from 'react-redux';
import { ENV } from '../../config/config';
import { getCategories, beforeCategory, addCategory, updateCategory, deleteCategory } from './Categories.action';
import FullPageLoader from 'components/FullPageLoader/FullPageLoader';
import Pagination from 'rc-pagination';
import 'rc-pagination/assets/index.css';
import localeInfo from 'rc-pagination/lib/locale/en_US';
import { Button, Card, Form, Table, Container, Row, Col, OverlayTrigger, Tooltip, Modal } from "react-bootstrap";
import imagePlaceholder from "assets/img/placeholder.png";
import { useHistory, useLocation } from 'react-router';
import moment from "moment";
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faFilter, faPlus, faUserPlus } from '@fortawesome/free-solid-svg-icons'

const Categories = (props) => {
    const history = useHistory();
    const location = useLocation()
    const searchQuery = new URLSearchParams(location.search);
    const [categories, setCategories] = useState(null)
    const [pagination, setPagination] = useState(null)
    const [loader, setLoader] = useState(true)
    const [viewShow, setViewShow] = useState(false);
    const [createShow, setCreateShow] = useState(false);
    const [editShow, setEditShow] = useState(false);
    const [delShow, setDelShow] = useState(false);
    const [delId, setDelId] = useState();
    const [filterCheck, setFilterCheck] = useState(false);
    const [filterMsgCheck, setFilterMsgCheck] = useState(false);
    const [formValues, setFormValues] = useState({
        name: ''
    });
    const [filters, setFilters] = useState({
        title: searchQuery.get("title"),
        status: searchQuery.get("status")
    });
    const [msg, setMsg] = useState({
        name: '',
        image: ''
    })

    useEffect(() => {
        toast.dismiss();
        window.scroll(0, 0);
    }, [])

    useEffect(() => {
        if (props.category.getCategories) {
            const { categories, pagination } = props.category;
            setCategories(categories);
            setPagination(pagination);
            props.beforeCategory()
        }
    }, [props.category.getCategories])

    useEffect(() => {
        if (props.category.changed)
            setFilters({
                title: null, status: null
            })
    }, [props.category.changed])

    useEffect(() => {
        if (categories) {
            setLoader(false)
        }
    }, [categories])

    // when an error is received
    useEffect(() => {
        if (props.error.error)
            setLoader(false)
    }, [props.error.error])

    useEffect(() => {
        onPageChange(1);
    }, [filters])

    const handleViewClose = () => setViewShow(false);

    const handleViewShow = () => setViewShow(true);

    const handleCreateClose = () => {
        setMsg({})
        setCreateShow(false);
    }

    const handleCreateShow = () => {
        setFormValues({
            name: '',
            image: ''
        });
        setCreateShow(true)
    };

    const handleEditClose = () => {
        setMsg({})
        setEditShow(false);
    }

    const handleEditShow = () => setEditShow(true);

    const handleDelClose = () => {
        setDelId();
        setDelShow(false);
    };

    const handleDelShow = (id) => {
        setDelId(id);
        setDelShow(true)
    };

    const onPageChange = async (page) => {
        const params = {};
        if (filters.title)
            params.title = filters.title;
        if (filters.status)
            params.status = filters.status;
        setLoader(true);
        const qs = ENV.objectToQueryString({ ...params, page });
        props.getCategories(qs);
        history.replace({
            pathname: location.pathname, search: ENV.objectToQueryString(params)
        });
    }

    const handleInputChange = (e) => {
        const target = e.target;
        const name = e.target.name;
        const value = e.target.value;
        if (name === "status") {
            if (target.checked)
                setFormValues({ ...formValues, [name]: true })
            else
                setFormValues({ ...formValues, [name]: false })
        }
        else
            setFormValues({ ...formValues, [name]: value });
    }

    const handleCreateCategory = (e) => {
        e.preventDefault();
        if (formValues.name && formValues.image) {
            setLoader(true);
            props.addCategory(formValues);
            handleCreateClose();
            setMsg({
                name: '',
                image: ''
            });
        }
        else {
            let msgObj = { ...msg }
            if (!formValues.name)
                msgObj.name = "Name Is Required."
            if (!formValues.image)
                msgObj.image = "Image Is Required."
            setMsg(msgObj)
        }
    }

    const handleEditCategory = (e) => {
        e.preventDefault();
        if (formValues.name && formValues.image) {
            setLoader(true);
            props.updateCategory(formValues);
            handleEditClose();
            setMsg({
                name: '',
                image: ''
            });
        }
        else {
            let msgObj = { ...msg }
            if (!formValues.name)
                msgObj.name = "Name Is Required."
            if (!formValues.image)
                msgObj.image = "Image Is Required."
            setMsg(msgObj)
        }
    }

    const handleDelCategory = () => {
        setLoader(true);
        props.deleteCategory({ id: delId });
        handleDelClose();
    }

    const handlePhotoChange = (event) => {
        const file = event.target.files;
        if (file.length === 0) {
            setFormValues({ ...formValues, image: null });
        } else if (checkMimeType(event)) {
            setFormValues({ ...formValues, image: file[0] });
        } else {
            setFormValues({ ...formValues, image: null });
            toast.error("Unsuccessful in choosing image file");
        }
    };

    const checkMimeType = (event) => {
        //getting file object
        let file = event.target.files;
        //define message container
        let err = "";
        // list allow mime type
        const types = ["image/png", "image/jpg", "image/jpeg", "image/svg", "image/gif"];

        // compare file type find doesn't matach
        if (types.every((type) => file[0].type !== type)) {
            // create error message and assign to container
            err = file[0].type + " is not a supported format";
        }

        if (err !== "") {
            // if message not same old that mean has error
            event.target.value = null; // discard selected file
            console.log(err);
            return false;
        }
        return true;
    };

    const rendFilters = () => {
        if (filterCheck) {
            return (
                <Form className="row mt-3" onSubmit={(e) => {
                    e.preventDefault();
                    const title = document.getElementById("searchTitle").value.trim();
                    const status = document.getElementById("statusSelect").value;
                    const obj = { ...filters };
                    if (title)
                        obj.title = title;
                    else
                        obj.title = null;
                    if (status !== "null")
                        obj.status = status;
                    else obj.status = null;
                    if (obj.title || obj.status) {
                        setFilterMsgCheck(false)
                        setFilters(obj);
                    }
                    else {
                        setFilterMsgCheck(true)
                    }
                }}>
                    <div className="col-md-3 p-0">
                        <label htmlFor="searchTitle">Title</label>
                        <input id="searchTitle" type="text" className="form-control" placeholder="Enter title..." defaultValue={filters.title} />
                    </div>
                    <div className="col-md-3">
                        <label htmlFor="statusSelect">Status</label>
                        <select id="statusSelect" className="form-control form-select" aria-label="Default select example">
                            <option value="null" selected={!filters.status ? true : false}>-- Select A Status --</option>
                            <option value="active" selected={filters.status === "active" ? true : false}>Active</option>
                            <option value="inactive" selected={filters.status === "inactive" ? true : false}>Inactive</option>
                        </select>
                    </div>
                    <div className="col-md-6 d-flex justify-content-end align-items-end p-0">
                        {(filters.title || filters.status) && <button className="btn btn-info mr-3 btn-warning" type="button" onClick={() => {
                            setFilters({
                                title: null, status: null
                            })
                        }}>Reset Filters</button>}
                        <button className="btn apply-filters blue-bg" type="submit" >Apply Filters</button>
                    </div>
                    <div className="col-md-12">
                        <span className={filterMsgCheck ? `` : `d-none`}>
                            <label className="pt-3 text-danger">Please fill a filter field to perform filteration</label>
                        </span>
                    </div>
                </Form>
            )
        }
    }

    return (
        <>
            {
                loader ?
                    <FullPageLoader />
                    :
                    <Container fluid>
                        <Row>
                            <Col md={12}>
                                <Card.Header className="mb-5 head-grid">
                                    <div className='d-flex justify-content-between align-items-center'>
                                        <Card.Title as="h4">Filter</Card.Title>
                                        <Button onClick={() => { setFilterCheck(!filterCheck); setFilterMsgCheck(false); }} className="yellow-bg m-0">
                                            <span>
                                                Filters
                                            </span>
                                            <span className="pl-1">
                                                <FontAwesomeIcon icon={faFilter} />
                                            </span>
                                        </Button>
                                    </div>
                                    <div>
                                        <div className='container-fluid'>
                                            {rendFilters()}
                                        </div>
                                    </div>
                                </Card.Header>
                            </Col>
                        </Row>
                        <Row>
                            <Col md="12">
                                <Card className="table-big-boy">
                                    <Card.Body className="table-full-width">
                                        <div className='d-flex justify-content-between align-items-center add-categories '>
                                            <Card.Title as="h4">Categories</Card.Title>
                                            <Form.Group as={Row} className="mb-0">
                                                <Col className="mt-1">
                                                    <Button
                                                        variant="info"
                                                        className="float-sm-right mr-1 yellow-bg"
                                                        onClick={handleCreateShow}>
                                                        Add Category
                                                        <span className="pl-1">
                                                            <FontAwesomeIcon icon={faPlus} />
                                                        </span>
                                                    </Button>
                                                </Col>
                                            </Form.Group>
                                        </div>
                                        <div className="table-full-width card-body">
                                            <div className=' table-responsive'>
                                                <Table className="table-striped w-full">
                                                    <thead>
                                                        <tr>
                                                            <th className="text-center td-start">#</th>
                                                            <th className="td-image">Image</th>
                                                            <th className="td-name">Name</th>
                                                            <th className="td-description">Description</th>
                                                            <th className="td-created">Created At</th>
                                                            <th className="td-status">Status</th>
                                                            <th className="td-actions">Action</th>
                                                        </tr>
                                                    </thead>
                                                    <tbody>
                                                        {
                                                            categories && categories.length ?
                                                                categories.map((category, index) => {
                                                                    return (
                                                                        <tr key={index}>
                                                                            <td className="text-center td-start">{pagination && ((pagination.limit * pagination.page) - pagination.limit) + index + 1}</td>

                                                                            <td className="td-image">
                                                                                <div className='user-image'><img src={category?.image} alt="categoryImage"></img></div>
                                                                            </td>
                                                                            <td className="td-name">
                                                                                {category?.name}
                                                                            </td>
                                                                            <td className="td-description">
                                                                                {category?.description}
                                                                            </td>
                                                                            <td className="td-created">
                                                                                {category.createdAt ? moment(category.createdAt).format('DD-MM-YYYY HH:MM:SS') : 'N/A'}
                                                                            </td>
                                                                            <td className="td-status">
                                                                                <span className={`text-white ${category.status ? `bg-success` : `bg-danger`}`}>
                                                                                    {category.status ? "active" : "inactive"}
                                                                                </span>
                                                                            </td>
                                                                            <td className="td-actions">
                                                                                <div className="d-flex">
                                                                                    <OverlayTrigger
                                                                                        overlay={
                                                                                            <Tooltip id="tooltip-897993903">
                                                                                                View
                                                                                            </Tooltip>
                                                                                        }
                                                                                    >
                                                                                        <Button
                                                                                            className="btn-link btn-xs"
                                                                                            onClick={() => {
                                                                                                const obj = {};
                                                                                                for (let key in category) {
                                                                                                    obj[key] = category[key];
                                                                                                }
                                                                                                setFormValues(obj);
                                                                                                handleViewShow();
                                                                                            }}
                                                                                            variant="info"
                                                                                        >
                                                                                            <i className="fas fa-image"></i>
                                                                                        </Button>
                                                                                    </OverlayTrigger>
                                                                                    <OverlayTrigger
                                                                                        overlay={
                                                                                            <Tooltip id="tooltip-436082023">
                                                                                                Edit
                                                                                            </Tooltip>
                                                                                        }
                                                                                    // placement="left"
                                                                                    >
                                                                                        <Button
                                                                                            className="btn-link btn-xs"
                                                                                            type="button"
                                                                                            variant="warning"
                                                                                            onClick={() => {
                                                                                                const obj = {};
                                                                                                for (let key in category) {
                                                                                                    obj[key] = category[key];
                                                                                                }
                                                                                                setFormValues(obj);
                                                                                                handleEditShow();
                                                                                            }}
                                                                                        >
                                                                                            <i className="fas fa-edit"></i>
                                                                                        </Button>
                                                                                    </OverlayTrigger>
                                                                                    <OverlayTrigger
                                                                                        overlay={
                                                                                            <Tooltip id="tooltip-481441726">Remove</Tooltip>
                                                                                        }
                                                                                    >
                                                                                        <Button
                                                                                            className="btn-link btn-xs"
                                                                                            onClick={() => handleDelShow(category._id)}
                                                                                            variant="danger"
                                                                                        >
                                                                                            <i className="fas fa-times"></i>
                                                                                        </Button>
                                                                                    </OverlayTrigger>
                                                                                </div>
                                                                            </td>
                                                                        </tr>
                                                                    )
                                                                })
                                                                :
                                                                <tr>
                                                                    <td colSpan="7" className="text-center">
                                                                        <span className="no-data-found d-block">No Categories found</span>
                                                                    </td>
                                                                </tr>
                                                        }
                                                    </tbody>
                                                </Table>
                                            </div>
                                            {pagination &&
                                                <Pagination
                                                    className="m-3"
                                                    defaultCurrent={1}
                                                    pageSize // items per page
                                                    current={pagination.page} // current active page
                                                    total={pagination.pages} // total pages
                                                    onChange={onPageChange}
                                                    locale={localeInfo}
                                                />
                                            }
                                        </div>

                                    </Card.Body>
                                </Card>
                            </Col>
                        </Row>
                    </Container>
            }

            {viewShow && <Modal show={viewShow} onHide={handleViewClose}>
                <Form>
                    <Modal.Header closeButton>
                        <Modal.Title className="yellow-color">View Category</Modal.Title>
                    </Modal.Header>
                    <Modal.Body>
                        <Form.Group className="mb-3">
                            <Form.Label>
                                Name
                            </Form.Label>
                            <Form.Control type="text" name="name" value={formValues?.name} disabled />
                        </Form.Group>

                        <Form.Group className="mb-3">
                            <Form.Label>Description</Form.Label>
                            <Form.Control as="textarea" rows={3} name="description" value={formValues?.description} readOnly />
                        </Form.Group>
                        <Form.Group className="mb-3">
                            <Form.Label className="mr-3">Image</Form.Label>
                            <div className='mb-2'>
                                <img src={formValues?.image} alt="categoryImage" style={{ height: 100, width: 100, objectFit: "contain" }} />
                            </div>
                        </Form.Group>
                        <Form.Group className="switch-wrapper mb-3">
                            <span className="d-block mb-2">Status</span>
                            <label class="switch">
                                <input type="checkbox" name="status" checked={formValues?.status} readOnly />
                                <span class="slider round"></span>
                            </label>
                        </Form.Group>
                    </Modal.Body>
                    <Modal.Footer>
                        <Button variant="danger" onClick={handleViewClose}>
                            Close
                        </Button>
                    </Modal.Footer>
                </Form>
            </Modal>}

            {createShow && <Modal show={createShow} onHide={handleCreateClose}>
                <Form onSubmit={handleCreateCategory}>
                    <Modal.Header closeButton>
                        <Modal.Title className='yellow-color'>Add Category</Modal.Title>
                    </Modal.Header>
                    <Modal.Body>

                        <Form.Group className="mb-3">
                            <Form.Label>
                                Name <span style={{ color: "red" }}>*</span>
                            </Form.Label>
                            <Form.Control type="text" placeholder="Enter name" maxLength={15} name="name" value={formValues?.name} onChange={handleInputChange} />
                            <span className={msg.name ? `` : `d-none`}>
                                <label className="pl-1 pt-0 text-danger">{msg.name}</label>
                            </span>
                        </Form.Group>

                        <Form.Group className="mb-3">
                            <Form.Label>Description</Form.Label>
                            <Form.Control as="textarea" rows={3} name="description" value={formValues?.description} onChange={handleInputChange} />
                        </Form.Group>
                        <Form.Group className="mb-3">
                            <Form.Label>Image <span style={{ color: "red" }}>*</span></Form.Label>
                            <div className='mb-2'>
                                <img src={formValues?.image ? URL.createObjectURL(formValues.image) : imagePlaceholder} alt="categoryImage" style={{ height: 100, width: 100, objectFit: "contain" }} />
                            </div>
                            <Form.Control type="file" onChange={handlePhotoChange} />
                            <span className={msg.image ? `` : `d-none`}>
                                <label className="pl-1 pt-0 text-danger">{msg.image}</label>
                            </span>
                        </Form.Group>
                        <Form.Group className="switch-wrapper mb-3">
                            <span className="d-block mb-2">Status</span>
                            <label class="switch">
                                <input type="checkbox" name="status" onChange={handleInputChange} />
                                <span class="slider round"></span>
                            </label>
                        </Form.Group>
                    </Modal.Body>
                    <Modal.Footer>
                        <Button variant="danger" onClick={handleCreateClose}>
                            Close
                        </Button>
                        <Button variant="primary" type="submit" className="yellow-bg">
                            Save Changes
                        </Button>
                    </Modal.Footer>
                </Form>
            </Modal>}

            {editShow && <Modal show={editShow} onHide={handleEditClose}>
                <Form onSubmit={handleEditCategory}>
                    <Modal.Header closeButton>
                        <Modal.Title className="yellow-color">Update Category</Modal.Title>
                    </Modal.Header>
                    <Modal.Body>

                        <Form.Group className="mb-3">
                            <Form.Label>
                                Name <span style={{ color: "red" }}>*</span>
                            </Form.Label>
                            <Form.Control type="text" placeholder="Enter name" name="name" value={formValues?.name} onChange={handleInputChange} />
                            <span className={msg.name ? `` : `d-none`}>
                                <label className="pl-1 pt-0 text-danger">{msg.name}</label>
                            </span>
                        </Form.Group>

                        <Form.Group className="mb-3">
                            <Form.Label>Description</Form.Label>
                            <Form.Control as="textarea" rows={3} name="description" value={formValues?.description} onChange={handleInputChange} />
                        </Form.Group>
                        <Form.Group className="mb-3">
                            <Form.Label>Image <span style={{ color: "red" }}>*</span></Form.Label>
                            <div className='mb-2'>
                                <img src={formValues?.image ? (typeof (formValues.image) === "string" ? formValues.image : URL.createObjectURL(formValues.image)) : imagePlaceholder} alt="categoryImage" style={{ height: 100, width: 100, objectFit: "contain" }} />
                            </div>
                            <Form.Control type="file" onChange={handlePhotoChange} required={formValues?.image ? false : true} />
                            <span className={msg.image ? `` : `d-none`}>
                                <label className="pl-1 pt-0 text-danger">{msg.image}</label>
                            </span>
                        </Form.Group>
                        <Form.Group className="switch-wrapper mb-3">
                            <span className="d-block mb-2">Status</span>
                            <label class="switch">
                                <input type="checkbox" name="status" checked={formValues?.status ? true : false} onChange={() => {
                                    if (formValues?.status)
                                        setFormValues({ ...formValues, status: false });
                                    else
                                        setFormValues({ ...formValues, status: true });
                                }} />
                                <span class="slider round"></span>
                            </label>
                        </Form.Group>
                    </Modal.Body>
                    <Modal.Footer>
                        <Button variant="danger" onClick={handleEditClose}>
                            Close
                        </Button>
                        <Button variant="primary" type="submit" className="yellow-bg">
                            Update Changes
                        </Button>
                    </Modal.Footer>
                </Form>
            </Modal>}

            {delShow && <Modal show={delShow} onHide={handleDelClose}>
                <Modal.Header closeButton>
                    <Modal.Title className="yellow-color delete-tag mb-5">Are you sure you want to delete this category?</Modal.Title>
                </Modal.Header>
                <Modal.Footer>
                    <Button variant="danger" onClick={handleDelClose}>
                        No
                    </Button>
                    <Button variant="primary" onClick={handleDelCategory} className="yellow-bg">
                        Yes
                    </Button>
                </Modal.Footer>
            </Modal>}
        </>
    )
}

const mapStateToProps = state => ({
    category: state.category,
    error: state.error
});

export default connect(mapStateToProps, { getCategories, beforeCategory, addCategory, updateCategory, deleteCategory })(Categories);