import { GET_CATEGORIES, BEFORE_CATEGORY, UPSERT_CATEGORY } from '../../redux/types';

const initialState = {
    categories: [],
    pagination: null,
    getCategories: false,
    changed: false
}

export default function (state = initialState, action) {
    switch (action.type) {
        case GET_CATEGORIES:
            return {
                ...state,
                categories: action.payload.categories,
                pagination: action.payload.pagination,
                getCategories: true
            }
        case BEFORE_CATEGORY:
            return {
                ...state,
                categories: [],
                pagination: null,
                getCategories: false,
                changed: false
            }
        case UPSERT_CATEGORY:
            return {
                ...state,
                changed: true
            }
        default:
            return {
                ...state
            }
    }
}