import React, { useState, useEffect } from "react";
import { connect } from "react-redux";
// import { ENV } from "../../config/config";
import {
  createUser,
  getUsers,
  beforeUser,
  updateUser,
  deleteUser,
} from "./RegisteredUsers.action";
import FullPageLoader from "components/FullPageLoader/FullPageLoader";
import Pagination from "rc-pagination";
import "rc-pagination/assets/index.css";
import localeInfo from "rc-pagination/lib/locale/en_US";
import {
  Button,
  Card,
  Form,
  Table,
  Container,
  Row,
  Col,
  OverlayTrigger,
  Tooltip,
  Modal,
} from "react-bootstrap";
import imagePlaceholder from "assets/img/placeholder.png";
import { useHistory, useLocation } from "react-router";
import moment from "moment";
import { toast } from "react-toastify";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faFilter, faUserPlus } from "@fortawesome/free-solid-svg-icons";
import userDefaultImg from "../../assets/img/placeholder.png";
import { ENV } from "../../config/config";

const RegisteredUsers = (props) => {
  const [_id, setId] = useState(window.atob(localStorage.getItem("adminId")));

  const history = useHistory();
  const location = useLocation();
  const searchQuery = new URLSearchParams(location.search);
  const [users, setUsers] = useState(null);
  const [pagination, setPagination] = useState(null);
  const [loader, setLoader] = useState(true);
  const [addShow, setAddShow] = useState(false);
  const [addForm, setAddForm] = useState({
    profileImage: "",
    status: false,
    type: 1,
    address: "Arhamsoft johar Town",
  });
  const [editShow, setEditShow] = useState(false);
  const [editForm, setEditForm] = useState({
    profileImage: "",
  });
  const [delModalCheck, setDelModalCheck] = useState(false);
  const [delId, selDelId] = useState(null);
  const [filterCheck, setFilterCheck] = useState(false);
  const [filterMsgCheck, setFilterMsgCheck] = useState(false);
  const [filters, setFilters] = useState({
    name: searchQuery.get("name"),
    email: searchQuery.get("email"),
    address: searchQuery.get("address"),
  });

  const [pic, setPic] = useState({
    image: null,
    _id,
  });

  useEffect(() => {
    toast.dismiss();
    window.scroll(0, 0);
    props.getUsers();
  }, []);

  useEffect(() => {
    if (props.user.getRegisteredUsers) {
      const { registeredUsers, pagination } = props.user;
      setUsers(registeredUsers);
      setPagination(pagination);
      // props.beforeUser();
    }
  }, [props.user.getRegisteredUsers, props.user.registeredUsers]);

  useEffect(() => {
    if (users) {
      setLoader(false);
    }
  }, [users]);

  useEffect (() => {
    
  } )

  // when an error is received
  useEffect(() => {
    if (props.error.error) setLoader(false);
  }, [props.error.error]);

  useEffect(() => {
    onPageChange(1);
  }, [filters]);

  useEffect(() => {
    if (props.user.updatedUserAuth) {
      const { user } = props.user.updatedUser;
      setUsers(
        users.map((item) => {
          if (item._id === user._id) {
            if (user.username) item.username = user.username;
            if (user.profileImage) item.profileImage = user.profileImage;
            if (user.email) item.email = user.email;
            if (user.address) item.address = user.address;
            if (user.status !== undefined) item.status = user.status;
          }
          return item;
        })
      );
      props.beforeUser();
      setLoader(false);
    }
  }, [props.user.updatedUserAuth]);

  useEffect(() => {
    if (props.user.delAuth) {
      const { success } = props.user.delUser;
      if (success) {
        props.beforeUser();
        setUsers(
          users.filter((item) => {
            if (item._id !== delId) {
              return item;
            }
          })
        );
      }
      setLoader(false);
    }
  }, [props.user.delAuth]);

  const onPageChange = async (page) => {
    const params = {};
    if (filters.name) params.name = filters.name;
    if (filters.email) params.email = filters.email;
    if (filters.address) params.address = filters.address;
    setLoader(true);
    const qs = ENV.objectToQueryString({ ...params, page });
    props.getUsers(qs);
    history.replace({
      pathname: location.pathname,
      search: ENV.objectToQueryString(params),
    });
  };

  const checkMimeType = (event) => {
    //getting file object
    let file = event.target.files;
    //define message container
    let err = "";
    // list allow mime type
    const types = [
      "image/png",
      "image/jpg",
      "image/jpeg",
      "image/svg",
      "image/gif",
    ];

    // compare file type find doesn't matach
    if (types.every((type) => file[0].type !== type)) {
      // create error message and assign to container
      err = file[0].type + " is not a supported format";
    }

    if (err !== "") {
      // if message not same old that mean has error
      event.target.value = null; // discard selected file
      return false;
    }
    return true;
  };

  const editPhotoChange = (event) => {
    let file = event.target.files[0];
    if (file) {
      let reader = new FileReader();
      reader.readAsDataURL(file);
      reader.onloadend = function () {
        setPic({ ...pic, image: reader.result });
      };
      setEditForm({ ...editForm, profileImage: file });
    }
  };

  const submitEdit = (e) => {
    e.preventDefault();
    let formData = new FormData()
    for (const key in editForm)
      formData.append(key, editForm[key])
    props.updateUser(formData);
    setEditShow(false);
    setLoader(false);
  };

  const delUser = () => {
    setDelModalCheck(false);
    props.deleteUser(delId);
    setLoader(true);
  };

  const addPhotoChange = (e) => {
    let file = e.target.files[0];
    if (file) {
      let reader = new FileReader();
      reader.readAsDataURL(file);
      reader.onloadend = function () {
        setPic({ ...pic, image: reader.result });
      };
      setAddForm({ ...addForm, profileImage: file });
    }
  };

  const addUser = (e) => {
    e.preventDefault();
    let formData = new FormData()
    for (const key in addForm)
      formData.append(key, addForm[key])
    props.createUser(formData);
    setAddForm({
      profileImage: "",
      status: false,
      type: 1,
      address: "Arhamsoft johar Town",
    })
    setAddShow(false);
    setLoader(true);
  };
  const rendFilters = () => {
    if (filterCheck) {
      return (
        <Form
          className="row mt-3"
          onSubmit={(e) => {
            e.preventDefault();
            const name = document.getElementById("searchName").value.trim();
            const email = document.getElementById("searchEmail").value.trim();
            const address = document.getElementById("searchAddress").value.trim();
            const obj = { ...filters };
            if (name) obj.name = name;
            else obj.name = null;
            if (email) obj.email = email;
            else obj.email = null;
            if (address) obj.address = address;
            else obj.address = null;
            if (name || email || address) {
              setFilterMsgCheck(false);
              setFilters(obj);
            } else {
              setFilterMsgCheck(true);
            }
          }}
        >
          <div className="col-md-3 p-0">
            <label htmlFor="searchName">Name</label>
            <input
              id="searchName"
              type="text"
              className="form-control"
              placeholder="Enter name..."
              defaultValue={filters.name}
            />
          </div>
          <div className="col-md-3">
            <label htmlFor="searchEmail">Email</label>
            <input
              id="searchEmail"
              type="text"
              className="form-control"
              placeholder="Enter email..."
              defaultValue={filters.email}
            />
          </div>
          <div className="col-md-3">
            <label htmlFor="searchAddress">Address</label>
            <input
              id="searchAddress"
              type="text"
              className="form-control"
              placeholder="Enter address..."
              defaultValue={filters.address}
            />
          </div>
          <div className="col-md-3 p-0 d-flex justify-content-end align-items-end">
            {(filters.name || filters.email || filters.address) && (
              <button
                className="btn btn-info mr-3 btn-warning"
                type="button"
                onClick={() =>
                  setFilters({
                    name: null,
                    email: null,
                    address: null,
                  })
                }
              >
                Reset Filters
              </button>
            )}
            <button className="btn apply-filters blue-bg" type="submit">
              Apply Filters
            </button>
          </div>
          <div className="col-md-12">
            <span className={filterMsgCheck ? `` : `d-none`}>
              <label className="pt-3 text-danger">
                Please fill a filter field to perform filteration
              </label>
            </span>
          </div>
        </Form>
      );
    }
  };

  return (
    <>
      {loader ? (
        <FullPageLoader />
      ) : (
        <Container fluid>
          <Row>
            <Col md={12}>
              <Card.Header className="mb-5 head-grid">
                <div className="d-flex justify-content-between align-items-center">
                  <Card.Title as="h4">Filter</Card.Title>
                  <Button
                    onClick={() => {
                      setFilterCheck(!filterCheck);
                      setFilterMsgCheck(false);
                    }}
                    className="yellow-bg m-1"
                  >
                    <span>Filters</span>
                    <span className="pl-1">
                      <FontAwesomeIcon icon={faFilter} />
                    </span>
                  </Button>
                </div>

                <div>
                  <div className="container-fluid">{rendFilters()}</div>
                </div>
              </Card.Header>
            </Col>
          </Row>
          <Row>
            <Col md="12">
              <Card className="table-big-boy">
                <div className="d-flex justify-content-between align-items-center register-users">
                  <Card.Title as="h4">Registered Users</Card.Title>
                  <Button
                    onClick={() => {
                      setAddShow(true);
                    }}
                    className="yellow-bg m-0"
                  >
                    <span>User</span>
                    <span className="pl-1">
                      <FontAwesomeIcon icon={faUserPlus} />
                    </span>
                  </Button>
                </div>

                <Card.Body className="table-full-width">
                  <div className=" table-responsive">
                    <Table className="table-striped w-full">
                      <thead>
                        <tr>
                          <th className="text-center td-start">#</th>
                          <th className="td-image">Image</th>
                          <th className="td-name">UserName</th>
                          <th className="td-email">Email</th>
                          <th className="td-address">Address</th>
                          <th className="td-status">Status</th>
                          <th className="td-created">Created At</th>
                          <th className="td-actions">Action</th>
                        </tr>
                      </thead>
                      <tbody>
                        {users && users.length ? (
                          users.map((user, index) => {
                            return (
                              <tr key={index}>
                                <td className="td-start text-center">
                                  {pagination &&
                                    pagination.limit * pagination.page -
                                    pagination.limit +
                                    index +
                                    1}
                                </td>

                                <td className="td-image">
                                  <div className="user-image">
                                    <img
                                      src={
                                        !user.profileImage ? imagePlaceholder : `${ENV.Backend_Img_Url}${user.profileImage}`
                                      }
                                      alt="userImage"
                                      className="img-fluid"
                                    ></img>
                                  </div>
                                </td>
                                <td className="td-name">{user?.username}</td>
                                <td className="td-email">{user?.email}</td>
                                <td className="td-address">{user?.address}</td>
                                <td className="td-status">
                                  <span
                                    className={`text-white ${user.status ? `bg-success` : `bg-danger`
                                      }`}
                                  >
                                    {user.status ? "active" : "inactive"}
                                  </span>
                                </td>
                                <td className="td-created">
                                  {user.createdAt
                                    ? moment(user.createdAt).format(
                                      "DD-MM-YYYY HH:MM:SS"
                                    )
                                    : "N/A"}
                                </td>
                                <td className="td-actions">
                                  <div className="d-flex">
                                    <OverlayTrigger
                                      overlay={
                                        <Tooltip id="tooltip-436082023">
                                          Edit
                                        </Tooltip>
                                      }
                                    // placement="left"
                                    >
                                      <Button
                                        className="btn-link btn-xs"
                                        type="button"
                                        variant="warning"
                                        onClick={() => {
                                          setEditForm({
                                            ...editForm,
                                            profileImage: user.profileImage,
                                            username: user.username,
                                            email: user.email,
                                            address: user.address,
                                            status: user.status,
                                            _id: user._id,
                                          });
                                          setEditShow(true);
                                        }}
                                      >
                                        <i className="fas fa-edit"></i>
                                      </Button>
                                    </OverlayTrigger>
                                    <OverlayTrigger
                                      overlay={
                                        <Tooltip id="tooltip-481441726">
                                          Remove
                                        </Tooltip>
                                      }
                                    >
                                      <Button
                                        className="btn-link btn-xs"
                                        onClick={() => {
                                          setDelModalCheck(true);
                                          selDelId(user._id);
                                        }}
                                        variant="danger"
                                      >
                                        <i className="fas fa-times"></i>
                                      </Button>
                                    </OverlayTrigger>
                                  </div>
                                </td>
                              </tr>
                            );
                          })
                        ) : (

                          <tr>
                            <td colSpan="8" className="text-center">
                              <span className="no-data-found d-block">No Users found</span>
                            </td>
                          </tr>
                        )}
                      </tbody>
                    </Table>
                  </div>
                  {pagination && (
                    <Pagination
                      className="m-3"
                      defaultCurrent={1}
                      pageSize // items per page
                      current={pagination.page} // current active page
                      total={pagination.pages} // total pages
                      onChange={onPageChange}
                      locale={localeInfo}
                    />
                  )}
                </Card.Body>
              </Card>
            </Col>
          </Row>
        </Container>
      )}

      {/* /////////// */}
      {addShow && (
        <Modal
          show={addShow}
          onHide={() => {
            setAddShow(false);
          }}
        >
          <Form
            onSubmit={(e) => {
              addUser(e);
            }}
          >
            <Modal.Header closeButton>
              <Modal.Title className="yellow-color">Add User</Modal.Title>
            </Modal.Header>
            <Modal.Body>
              <Form.Group className="mb-3">
                <Form.Label>Name</Form.Label>
                <Form.Control
                  type="text"
                  placeholder="Enter name"
                  name="name"
                  value={addForm.username}
                  onChange={(e) => {
                    setAddForm({ ...addForm, username: e.target.value });
                  }}
                // required
                />
              </Form.Group>
              <Form.Group className="mb-3">
                <Form.Label>Email</Form.Label>
                <Form.Control
                  type="email"
                  placeholder="Enter email"
                  name="email"
                  value={addForm.email}
                  onChange={(e) => {
                    setAddForm({ ...addForm, email: e.target.value });
                  }}
                // required
                />
              </Form.Group>

              <Form.Group className="mb-3">
                <Form.Label>Password</Form.Label>
                <Form.Control
                  type="password"
                  placeholder="Enter password"
                  name="password"
                  value={addForm.password}
                  onChange={(e) => {
                    setAddForm({ ...addForm, password: e.target.value });
                  }}
                // required
                />
              </Form.Group>
              <Form.Group className="mb-3">
                <Form.Label>Address</Form.Label>
                <Form.Control
                  type="text"
                  rows={3}
                  name="address"
                  value={addForm.address}
                  defaultValue={"ArrhamSoft Johar Town"}
                  disabled
                />
              </Form.Group>
              <Form.Group className="mb-3">
                <Form.Label>Image</Form.Label>
                <div className="mb-2">
                  <img
                    src={!addForm.profileImage ? userDefaultImg : pic.image}
                    style={{ height: 100, width: 100, objectFit: "contain" }}
                  />
                </div>
                <Form.Control type="file" onChange={addPhotoChange} />
              </Form.Group>
              <Form.Group className="switch-wrapper mb-3">
                <span className="d-block mb-2">Status</span>
                <label class="switch">
                  <input
                    type="checkbox"
                    name="status"
                    checked={addForm.status ? true : false}
                    onChange={(e) => {
                      setAddForm({ ...addForm, status: e.target.checked });
                    }}
                  />
                  <span class="slider round"></span>
                </label>
              </Form.Group>
            </Modal.Body>
            <Modal.Footer>
              <Button
                variant="danger"
                onClick={(e) => {
                  setAddShow(false);
                }}
              >
                Close
              </Button>
              <Button variant="primary" type="submit" className="yellow-bg">
                Add User
              </Button>
            </Modal.Footer>
          </Form>
        </Modal>
      )}

      {/* ///////// */}

      {editShow && (
        <Modal
          show={editShow}
          onHide={() => {
            setEditShow(false);
          }}
        >
          <Form
            onSubmit={(e) => {
              submitEdit(e);
            }}
          >
            <Modal.Header closeButton>
              <Modal.Title className="yellow-color">Update User</Modal.Title>
            </Modal.Header>
            <Modal.Body>
              <Form.Group className="mb-3">
                <Form.Label>Username</Form.Label>
                <Form.Control
                  type="text"
                  placeholder="Enter name"
                  name="name"
                  value={editForm.username}
                  onChange={(e) => {
                    setEditForm({ ...editForm, username: e.target.value });
                  }}
                />
              </Form.Group>
              <Form.Group className="mb-3">
                <Form.Label>Email</Form.Label>
                <Form.Control
                  type="text"
                  placeholder="Enter name"
                  name="name"
                  value={editForm.email}
                  onChange={(e) => {
                    setEditForm({ ...editForm, email: e.target.value });
                  }}
                />
              </Form.Group>
              <Form.Group className="mb-3">
                <Form.Label>Address</Form.Label>
                <Form.Control
                  type="text"
                  rows={3}
                  name="description"
                  value={editForm.address}
                  onChange={(e) => {
                    setEditForm({ ...editForm, address: e.target.value });
                  }}
                  disabled
                />
              </Form.Group>
              <Form.Group className="mb-3">
                <Form.Label>Image</Form.Label>
                <div className="mb-2">
                  <img
                    src={editForm.profileImage ? typeof editForm.profileImage === 'string' ? `${ENV.Backend_Img_Url}${editForm.profileImage}` : URL.createObjectURL(editForm.profileImage) : imagePlaceholder}
                    style={{ height: 100, width: 100, objectFit: "contain" }}
                  />
                </div>
                <Form.Control type="file" onChange={editPhotoChange} />
              </Form.Group>
              <Form.Group className="switch-wrapper mb-3">
                <span className="d-block mb-2">Status</span>
                <label class="switch">
                  <input
                    type="checkbox"
                    name="status"
                    checked={editForm.status ? true : false}
                    onChange={(e) => {
                      setEditForm({ ...editForm, status: e.target.checked });
                    }}
                  />
                  <span class="slider round"></span>
                </label>
              </Form.Group>
            </Modal.Body>
            <Modal.Footer>
              <Button
                variant="danger"
                onClick={(e) => {
                  setEditShow(false);
                }}
              >
                Close
              </Button>
              <Button variant="primary" type="submit" className="yellow-bg">
                Update Changes
              </Button>
            </Modal.Footer>
          </Form>
        </Modal>
      )}

      {delModalCheck && (
        <Modal show={delModalCheck} onHide={() => setDelModalCheck(false)}>
          <Modal.Header closeButton>
            <Modal.Title className="yellow-color delete-tag mb-5">
              Are you sure you want to delete this user?
            </Modal.Title>
          </Modal.Header>
          <Modal.Footer>
            <Button variant="danger" onClick={() => setDelModalCheck(false)}>
              No
            </Button>
            <Button variant="primary" onClick={delUser} className="yellow-bg">
              Yes
            </Button>
          </Modal.Footer>
        </Modal>
      )}
    </>
  );
};

const mapStateToProps = (state) => ({
  user: state.user,
  error: state.error,
});

export default connect(mapStateToProps, {
  createUser,
  getUsers,
  beforeUser,
  updateUser,
  deleteUser,
})(RegisteredUsers);
