import {
    CREAT_USER,
    GET_USERS,
    BEFORE_USER,
    UPSERT_USER,
    DELETE_USER,
  } from "../../redux/types";
  
  const initialState = {
      // newUsers: [],
    registeredUsers: [],
    pagination: null,
    getRegisteredUsers: false,
    updatedUser: null,
    updatedUserAuth: false,
    delAuth: false,
    delUser: null,
    createdUser: null,
    createdUserAuth: false,
  };
  
  export default function (state = initialState, action) {
    switch (action.type) {
      case CREAT_USER:
        return { 
          ...state,
          getRegisteredUsers: true,
          registeredUsers: [ action.payload ,...state.registeredUsers] ,
        };
      case GET_USERS:
        return {
          ...state,
          registeredUsers: action.payload.users,
          pagination: action.payload.pagination,
          getRegisteredUsers: true,
        };
      case BEFORE_USER:
        return {
          ...state,
          registeredUsers: [],
          pagination: null,
          getRegisteredUsers: false,
          updatedUser: null,
          updatedUserAuth: false,
          delAuth: false,
          delUser: null,
          createdUserAuth: false,
        };
      case UPSERT_USER:
        return {
          ...state,
          updatedUser: action.payload,
          updatedUserAuth: true,
        };
      case DELETE_USER:
        return {
          ...state,
          delAuth: true,
          delUser: action.payload,
        };
      default:
        return {
          ...state,
        };
    }
  }
  